import axios from '@/plugins/axios'

// 
export const skills = data => axios({
  url: 'abilities',
  method: 'get',
  params: data || {}
})

// 
export const addSkill = data => axios({
  url: 'abilities',
  method: 'post',
  data: data
})

// 
export const delSkill = id => axios({
  url: `abilities/${id}`,
  method: 'delete'
})

// 
export const skill = id => axios({
  url: `abilities/${id}`,
  method: 'get'
})

// 
export const editSkill = (data, id) => axios({
  url: `abilities/${id}`,
  method: 'put',
  data: data
})
