import axios from '@/plugins/axios'

// 
export const companys = data => axios({
  url: 'companies',
  method: 'get',
  params: data || {}
})

// 
export const company = id => axios({
  url: `companies/${id}`,
  method: 'get'
})

// 
export const addCompany = data => axios({
  url: 'companies',
  method: 'post',
  data: data
})

// 
export const editCompany = (data, id) => axios({
  url: `companies/${id}`,
  method: 'put',
  data: data
})

// 
export const delCompany = id => axios({
  url: `companies/${id}`,
  method: 'delete'
})
