import axios from '@/plugins/axios'

// 
export const jobs = data => axios({
  url: 'jobs',
  method: 'get',
  params: data || {}
})

//  
export const editJob = (data, id) => axios({
  url: `jobs/${id}`,
  method: 'put',
  data: data
})

//  
export const addJob = data => axios({
  url: 'jobs',
  method: 'post',
  data: data
})

//  
export const WorkerOption = data => axios({
  url: 'jobs/data',
  method: 'get',
  params: data || {}
})
