import axios from '@/plugins/axios'

// 
export const works = data => axios({
  url: 'job-types',
  method: 'get',
  params: data || {}
})

// 
export const work = id => axios({
  url: `job-types/${id}`,
  method: 'get'
})

// 
export const editWork = (data, id) => axios({
  url: `job-types/${id}`,
  method: 'put',
  data: data
})
