/*
  url
*/
export const url = [
  {
    required: true,
    validator (rule, value, callback) {
      if (value === '' || value === undefined) {
        callback(new Error(i18n.t('tips.pleaseInputURL')))
      } else if (
        !/^http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- ./?%&=]*)?$/i.test(value)
      ) {
        callback(new Error(i18n.t('tips.pleaseInputCorrectURL')))
      } else {
        callback()
      }
    },
    trigger: 'blur'
  }
]

